.NavbarItems{
    background: #000839;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}


.navbar-logo {
    color:#ffa41b;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;

}


.fa-loopbook {
    margin-left: 0.5rem;
    font-size: 1.6rem;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(5,auto);
    grid-gap:10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content:end;
    margin-right: 2rem;
   

}

.nav-links {
    color: #ffa41b;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover{
    background-color: blue;
    border-radius: 4px;
    transition: all 0.2s ease-out;

}
.fa-bars {
    color: white;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}
